<template>
	<div class="cont">
		<div class="singername">
			<span>{{singer.name}}</span>
		</div>
		<div class="lbt">
			<el-carousel :interval="4000" type="card" height="280px" style="width:800px">			
				<el-carousel-item v-for="item in singer" :key="item.id">
					<img :src="singer.picUrl">
				</el-carousel-item>		
			</el-carousel>
		</div>
		<div class="tabs">
			<el-tabs v-model="activeName" @tab-click="handleClick(activeName)">
			    <el-tab-pane label="热门作品" name="first">
					<el-table :data="songs" stripe style="width: 100%">
						<el-table-column type="index" label="序号" width="90"></el-table-column>
						<el-table-column width="45px">
							<template slot-scope="scope">
								<i class="el-icon-video-play" @click="playMusic(singerSongId[scope.$index])"></i>
							</template>
						</el-table-column>
						<el-table-column prop="name" label="歌名" width="240"></el-table-column>
						<el-table-column prop="dt" label="时长" width="150">
							<template slot-scope="scope">
								<span>{{scope.row.dt/1000 | timeFormat("")}}</span>
							</template>
						</el-table-column>
						<el-table-column prop="al.name" label="所属专辑" width="270"></el-table-column>
<!-- 						<el-table-column width="100"><el-button icon="el-icon-success"></el-button></el-table-column>							
 -->				</el-table>
						<audio style="width: 100%;" :src="musicUrl" controls="controls" autoplay="autoplay" @play="play"
							@pause="pause">
						</audio>	
				</el-tab-pane>		
			    <el-tab-pane label="艺人介绍" name="second">
					<div class="artistdec" v-if="!!introduction[0]">
						<h3>						
							<p><i>&nbsp;</i>{{introduction[0] && introduction[0].ti}}</p>
						</h3>
						<p>{{introduction[0] && introduction[0].txt}}</p>
						<h3>
							<p><i>&nbsp;</i>{{introduction[1] && introduction[1].ti}}</p>
						</h3>
						<p>{{introduction[1] && introduction[1].txt}}</p>
						<h3>
							<p><i>&nbsp;</i>{{introduction[2] && introduction[2].ti}}</p>
						</h3>
						<p>{{introduction[2] && introduction[2].txt}}</p>
					</div>
					<div class="artistdec1" v-else="">
						<p>哎呀！暂无数据哦~</p>
					</div>
				</el-tab-pane>
			  </el-tabs>	
		</div>	
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		data () {
			return {	
				activeName:"",
				singer:[],//存放歌手信息
				songs:[],//存放歌曲信息
				introduction:[],
				musicUrl:"",
				singerSongId:[],
			}
		},
		created(){
				  this.getSinger();//获取歌手信息
				  this.getSongs();//获取歌曲信息
				//  this.getAlbum()
		},
		methods: {
			//轮播图
			getSinger(){
				var that=this;
				var id=this.$store.state.singerId;
				axios.get("https://autumnfish.cn/artists/detail?id="+id).then((res)=>{
					that.singer=res.data.artist;
				},(err)=>{})
			},
			//热门作品
			getSongs(){
				var that=this;
				var id=this.$store.state.singerId;
				/* console.log(id); */
				axios.get("https://autumnfish.cn/artist/top/song?id="+id+"&limit=50").then((res)=>{
					that.songs=res.data.songs;
					for(var i=0;i<50;i++){
						that.singerSongId[i]=res.data.songs[i].id;
					}
				},(err)=>{})
			},
			//艺人介绍
			getIntroduction(){
				var that=this;
				var id=this.$store.state.singerId;
				/* console.log(id); */
				axios.get("https://autumnfish.cn/artist/desc?id="+id).then((res)=>{
					that.introduction=res.data.introduction;
				},(err)=>{})
			},
		    handleClick(name) {
				console.log(name);
				if(name=='first'){
					this.getSongs();
				}
				if(name=='second'){
					this.getIntroduction();
				}
		    },
			playMusic(id){
				var that=this;
				//获取歌曲链接
				this.$axios.get("https://autumnfish.cn/song/url?id="+id)
				.then((response)=>{
					that.musicUrl=response.data.data[0].url;
				},(err)=>{})
			},
			play(){
				this.isPlaying=true;
			},
			pause(){
				this.isPlaying=false;
			},
		}
	};
</script>
	
<style scoped>
	.cont{
		margin:0 auto;
		width: 80%;
		height: 1500px;
	}
	.singername{
		margin: 0 auto;
		width: 80%;
		height: 30px;
		margin-top:20px;
		margin-bottom: 20px;
	}
	.singername span{
		color: #282B2E;
		font-size: 25px;
		font-weight: normal;
	}
	.lbt{
		width :800px;
		margin: 0 auto;
		box-shadow:10px 10px 20px 10px rgba(151, 147, 148, 0.5);		
	}
	.lbt img{
		width: 100%;
		height: 100%;
	}			
	.tabs{
		width: 80%;
		margin: 0 auto;
		margin-top: 30px;
	}
	.pic{
		margin-top: 20px;
		height: 320px;
		width: 1000px;
	}
	.pic1{
		padding: none;
		margin: 0 auto;
		float:left;
		width: 195px;
		height: 195px;
	}
	.pic1 img{
		margin: 0 auto;		
		height: 70%;
	}	
	.artistdec h3 i {
	    height: 14px;
	    width: 3px;
	    margin-right: 7px;
	    background: #c10d0c;
	}	
	.artistdec1{
		width: 100%;
	}
	.artistdec1 p{
		width: 265px;
		font-size: 30px;
		font-family: "FZShuTi";
		margin: 0 auto;
	}
</style>
